import Vue from 'vue';
import Vuex from 'vuex';

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    // 选中的顶部导航的模块
    chooseTobModule: JSON.parse(localStorage.getItem('chooseTobModule')) || {
      dataComponentKey: "CategoryModuleData",
      moduleKey: "tuijian",
      moduleName: "推荐",
      moduleType: "Category"
    },
    // 选中的专辑
    chooseAlbum: JSON.parse(localStorage.getItem('chooseAlbum')) || null,
    // 选中的听单模块
    chooseTing: JSON.parse(localStorage.getItem('chooseTing')) || { cid: 0, name: "remen", pageView: 33521023, title: "热门" },
    // 音频控件的基本参数
    controlsParams: JSON.parse(localStorage.getItem('controlsParams')) || {
      // 当前播放的音频的id
      id: '',
      // 当前播放的音频的名称
      title: '',
      // 当前播放的音频的路径
      palyPath: '',
      // 当前播放的音频的图片
      cover: '',
      // 当前进度
      currentRate: 0,
      // 目标进度
      rate: 100,
      // 歌曲是否播放
      isPaly: false,
      // 是否显示控件
      isShowControls: true
    },
    // 当前正在播放的音频列表的基本信息
    playingYinGuiObj: JSON.parse(localStorage.getItem('playingYinGuiObj')) || {
      // 当前正在播放的专辑的id
      albumId: '',
      albumTitle: '',
      albumCover: '',
      YinGuiList: [],
      nowYinGuiPage: 1,
      maxYinGuiNum: 0
    },
    // 当前正在预览的音频列表的基本信息
    lookingYinGuiObj: {
      // 当前正在预览的专辑的id
      albumId: '',
      albumTitle: '',
      albumCover: '',
      YinGuiList: [],
      nowYinGuiPage: 1,
      maxYinGuiNum: 0
    },
    //播放历史，播放过的专辑（包括音频的播放进度）
    playAlbumsHistory: JSON.parse(localStorage.getItem('playAlbumsHistory')) || [],
    // 我的收藏
    myCollect: JSON.parse(localStorage.getItem('myCollect')) || [],
    // 是否显示播放列表
    isShowPlayList: false
  },
  getters: {
  },
  mutations: {
    setChooseTobModule: function (state, param) {
      state.chooseTobModule = param.value;
    },
    setChooseAlbum: function (state, param) {
      state.chooseAlbum = param.value;
    },
    setChooseTing: function (state, param) {
      state.chooseTing = param.value;
    },
    setControlsParams: function (state, param) {
      state.controlsParams = param.value;
    },
    setPlayingYinGuiObj: function (state, param) {
      state.playingYinGuiObj = param.value;
    },
    setLookingYinGuiObj: function (state, param) {
      state.lookingYinGuiObj = param.value;
    },
    setPlayAlbumsHistory: function (state, param) {
      state.playAlbumsHistory = param.value;
    },
    setIsShowPlayList: function (state, param) {
      state.isShowPlayList = param.value;
    },
    setMyCollect: function (state, param) {
      state.myCollect = param.value;
    }
  },
  actions: {
  },
  modules: {
  }
})
